import { initWelcome } from "./pages/welcome";
import { initInstructions } from "./pages/instructions";
import { initPlay } from "./pages/play";
import { initResults } from "./pages/results";

const routes = [
  { path: /welcome/, component: initWelcome },
  { path: /instructions/, component: initInstructions },
  { path: /play/, component: initPlay },
  { path: /results/, component: initResults },
];

export function initRouter(container: Element) {
  // Ahora usamos el hash (#) para navegar
  function goTo(path: string) {
    window.location.hash = path;
  }

  function handleRoute(route: string) {
    console.log("El router recibió la ruta (hash):", route);

    // Limpiamos la ruta para que el regex la entienda sin el #
    const cleanRoute = route.replace("#", "");
    let matched = false;

    for (const r of routes) {
      if (r.path.test(cleanRoute)) {
        matched = true;
        const el = r.component({ goTo: goTo });

        if (container.firstChild) {
          container.firstChild.remove();
        }
        container.appendChild(el);
        break;
      }
    }

    // Redirección si no encuentra nada o está vacío
    if (!matched && (cleanRoute === "/" || cleanRoute === "")) {
      goTo("/welcome");
    }
  }

  // Escuchamos el cambio de Hash (fundamental para GitHub Pages)
  window.addEventListener("hashchange", () => {
    handleRoute(window.location.hash);
  });

  // Al cargar la página por primera vez, verificamos si ya hay un hash o vamos al inicio
  if (window.location.hash) {
    handleRoute(window.location.hash);
  } else {
    handleRoute("#/welcome");
  }
}